import { Moment } from 'moment';
import * as React from 'react';
import { Field, Form, InjectedFormProps, reduxForm } from 'redux-form';

import {
    AntdCol,
    AntdRow,
    FormError,
    renderDatePickerField,
    renderInput,
    renderInputNumber,
    renderSelectField,
    renderTextArea,
    renderUploadField,
    RenderUploadPictureField
} from '@/components';
import { FurnitureComponent, Order, orderUtils } from '@/restful';

export interface CreateComponentFormProps {

}

export interface CreateComponentFormValues extends
    Pick<FurnitureComponent, 'name'>,
    Pick<FurnitureComponent, 'quotaValue'>,
    Pick<FurnitureComponent, 'price'>,
    Pick<FurnitureComponent, 'displayName'> {
}

class CreateComponentFormComponent extends React.Component<
    CreateComponentFormProps &
    InjectedFormProps<CreateComponentFormValues, CreateComponentFormProps>
    > {
    render() {
        const { handleSubmit, error } = this.props;
        return (
            <Form onSubmit={handleSubmit}>
                <FormError error={error} />
                <AntdRow gutter={15}>
                    <AntdCol span={12}>
                        <Field
                            name={"name"}
                            component={RenderUploadPictureField}
                            label="nh hin th"
                            required={true}
                            uploadProps={{
                                placeholder: 'Chn nh'
                            }}
                        />
                    </AntdCol>
                    <AntdCol span={12}>
                        <Field
                            name={"name"}
                            component={renderUploadField}
                            label="OBJ file"
                            required={true}
                            uploadProps={{
                                placeholder: 'Chn MTL file',
                                accept: '.obj'
                            }}
                        />
                    </AntdCol>
                    <AntdCol span={12}>
                        <Field
                            name={"name"}
                            component={renderUploadField}
                            label="OBJ file"
                            required={true}
                            uploadProps={{
                                placeholder: 'Chn MTL file',
                                accept: '.mtl'
                            }}
                        />
                    </AntdCol>
                    <AntdCol span={12}>
                        <Field
                            name={"name"}
                            component={renderInput}
                            label="Tn cu kin"
                            required={true}
                            inputProps={{
                                placeholder: 'Nhp tn cu kin'
                            }}
                        />
                    </AntdCol>
                    <AntdCol span={12}>
                        <Field
                            name={"displayName"}
                            component={renderInput}
                            label="Tn hin th"
                            required={true}
                            inputProps={{
                                placeholder: 'Nhp tn hin th'
                            }}
                        />
                    </AntdCol>
                    <AntdCol span={12}>
                        <Field
                            name={"quotaValue"}
                            component={renderInputNumber}
                            label="nh mc vt liu"
                            required={true}
                            inputProps={{
                                placeholder: 'Nhp nh mc vt liu',
                                className: 'w-100'
                            }}
                        />
                    </AntdCol>
                    <AntdCol span={12}>
                        <Field
                            name={"price"}
                            component={renderInputNumber}
                            label="Gi"
                            required={true}
                            inputProps={{
                                placeholder: 'Nhp gi',
                                className: 'w-100'
                            }}
                        />
                    </AntdCol>
                </AntdRow>
            </Form>
        );
    }
}

export const CreateComponentForm = reduxForm<CreateComponentFormValues, CreateComponentFormProps>({
    form: 'UpdateOrder'
})(CreateComponentFormComponent);