import 'ant-design-pro/lib/PageHeader/style/index.less';
import 'ant-design-pro/lib/DescriptionList/style/index.less';

import PageHeader from 'ant-design-pro/lib/PageHeader';
import * as React from 'react';
import styled from 'styled-components';

import { AccessControl, AllowAccess, DenyAccess } from '@/app';
import { AntdButton, AntdCol, AntdIcon, AntdRow, AntdTag } from '@/components';
import { colorPrimary } from '@/configs';
import { Order, orderDetailUtils, orderUtils } from '@/restful';
import { formatCurrency, formatDate } from '@/utilities';

const AntdDescriptionList = require('ant-design-pro/lib/DescriptionList');

const PageHeaderWrapper = styled.div`
    margin: 0 0 30px 0;
`;

const OrderId = styled.span`
    color: ${colorPrimary};
`;

export interface OrderDetailHeaderProps {
    readonly order: Order;
    readonly onOrderCancel: (order: Order) => void;
    readonly onOrderChange: (order: Order) => void;
    readonly onUpdateOrderClick: (order: Order) => void;
}

export class OrderDetailHeader extends React.Component<OrderDetailHeaderProps> {
    render() {
        const { order, onOrderCancel, onOrderChange, onUpdateOrderClick } = this.props;
        const statusInfo = orderUtils.getStatusInfo(order);

        return (
            <PageHeaderWrapper>
                <PageHeader
                    logo={<img alt="" src="https://gw.alipayobjects.com/zos/rmsportal/nxkuOJlFJuAUhzlMTCEe.png" />}
                    title={<React.Fragment>Order: <OrderId>{order.code}</OrderId></React.Fragment>}
                    content={(
                        <AntdDescriptionList title={order.note || 'Chi tit:'} size="small" col={2}>
                            <AntdDescriptionList.Description term="Ngy t">
                                {formatDate(order.createdAt, 'DD-MM-YYYY HH:mm')}
                            </AntdDescriptionList.Description>
                            <AntdDescriptionList.Description term="S lng">
                                {orderDetailUtils.getQuantity(order.orderDetails)} sn phm
                            </AntdDescriptionList.Description>
                            <AntdDescriptionList.Description term="Tng gi tr sn phm">
                                {formatCurrency(order.totalPrice)}
                            </AntdDescriptionList.Description>
                            {
                                order.productsDiscount && (
                                    <AntdDescriptionList.Description term="Gim gi sn phm">
                                        {formatCurrency(order.productsDiscount)}
                                    </AntdDescriptionList.Description>
                                )
                            }
                            {
                                order.promotion && (
                                    <AntdDescriptionList.Description
                                        term={`M khuyn mi #${order.promotion.code}`}
                                    >
                                        -{formatCurrency(order.promotionDiscount)}
                                    </AntdDescriptionList.Description>
                                )
                            }
                            <AntdDescriptionList.Description term="Ph vn chuyn">
                                {formatCurrency(order.shippingFee)}
                            </AntdDescriptionList.Description>
                            <AntdDescriptionList.Description term="Yu cu t cc">
                                {formatCurrency(order.depositRequired)}
                            </AntdDescriptionList.Description>
                            <AntdDescriptionList.Description term="D kin giao hng">
                                {formatDate(order.shippingDate, 'DD-MM-YYYY')}
                            </AntdDescriptionList.Description>
                            <AntdDescriptionList.Description term="in thoi">
                                {order.phone}
                            </AntdDescriptionList.Description>
                            <AntdDescriptionList.Description term="Email">
                                {order.email}
                            </AntdDescriptionList.Description>
                            <AntdDescriptionList.Description term="Tnh thnh">
                                {order.shippingToCity.name}
                            </AntdDescriptionList.Description>
                            <AntdDescriptionList.Description term="a ch nhn hng">
                                {order.shippingAddress}
                            </AntdDescriptionList.Description>
                            <AntdDescriptionList.Description term="Tnh trng">
                                <AntdTag color={statusInfo.color}>
                                    <AntdIcon type={statusInfo.icon} /> {statusInfo.label}
                                </AntdTag>
                            </AntdDescriptionList.Description>
                        </AntdDescriptionList>
                    )}
                    action={(
                        <AccessControl allowRoles="root">
                            <AllowAccess>
                                <AntdButton
                                    icon="edit"
                                    onClick={() => onUpdateOrderClick(order)}
                                >
                                    Cp nht n hng
                                </AntdButton>
                            </AllowAccess>
                            <DenyAccess>
                                <AntdButton.Group>
                                    {
                                        orderUtils.canChange(order) && (
                                            <AntdButton
                                                type="danger"
                                                ghost={true}
                                                icon="rollback"
                                                onClick={() => onOrderChange(order)}
                                            >
                                                i tr
                                            </AntdButton>
                                        )
                                    }
                                    {
                                        orderUtils.canCancel(order) && (
                                            <AntdButton
                                                type="danger"
                                                ghost={true}
                                                icon="delete"
                                                onClick={() => onOrderCancel(order)}
                                            >
                                                Hy n hng
                                            </AntdButton>
                                        )
                                    }
                                </AntdButton.Group>
                            </DenyAccess>
                        </AccessControl>

                    )}
                    extraContent={(
                        <AntdRow>
                            <AntdCol sm={24} md={12}>
                                <div style={{ color: 'rgba(0, 0, 0, 0.43)' }}>Cn thanh ton</div>
                                <div style={{ color: 'rgba(0, 0, 0, 0.85)', fontSize: 20 }}>
                                    {formatCurrency(order.totalOfPayment)}
                                </div>
                            </AntdCol>
                            <AntdCol sm={24} md={12}>
                                <div style={{ color: 'rgba(0, 0, 0, 0.43)' }}> thanh ton</div>
                                <div style={{ color: 'rgba(0, 0, 0, 0.85)', fontSize: 20 }}>
                                    {formatCurrency(order.theAmountPaid)}
                                </div>
                            </AntdCol>
                        </AntdRow>
                    )}
                />
            </PageHeaderWrapper>
        );
    }
}